$bg-primary: #2b2b2b;
$bg-secondary: #333333;

$txt-primary: #ffffff;
$txt-secondary: rgba(255, 255, 255, 0.4);
$txt-dark: #333333;
$txt-light: #2b2b2b;

$btn-yellow: #e8bf59;
$btn-violet: #a490e6;

$mobile-width: 400px;

@mixin header1-desktop {
  font-size: 60px;
  font-weight: 600;
  line-height: 90px;
  color: $txt-primary;
  @media (max-width: $mobile-width) {
    font-size: 40px;
  }
}

@mixin header2-desktop {
  font-size: 24px;
  font-weight: 600;
  line-height: 24px;
  color: $txt-primary;
}
@mixin header3-desktop {
  font-size: 18px;
  font-weight: 600;
  line-height: 32px;
  color: $txt-primary;
}

@mixin content1-desktop {
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: $txt-secondary;
}

@mixin content2-desktop {
  font-size: 24px;
  font-weight: 600;
  line-height: 40px;
  color: $txt-secondary;
}

@mixin display-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: $mobile-width) {
    flex-direction: column;
  }
}

@mixin btn-violet {
  background-color: $btn-violet;
  padding: 9px 62px;
  @include content1-desktop;
  color: $txt-dark;
  border-radius: 15px;
  margin-left: 15px;
  border: 0;
  outline: none;
  cursor: pointer;
  &:hover {
    opacity: 0.7;
  }
  transition: 0.2s all linear;
}

@mixin btn-text {
  background-color: transparent;
  border: 0;
  margin-right: 15px;
  outline: none;
  cursor: pointer;
  @include content1-desktop;
  color: $txt-primary;
  &:hover {
    opacity: 0.7;
  }
  transition: 0.2s all linear;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: $bg-primary;
  padding: 25px 50px 50px;
}

.main {
  flex: 1;
  hr {
    border: 2px solid  $bg-secondary;
    margin-bottom: 5%;
  }
}

.logo {
  @include header2-desktop;
}

.header,
.footer {
  @include display-flex;
  .button-text {
    @include btn-text;
  }
  .button-violet {
    @include btn-violet;
  }
}
.header {
  @media(max-width: 500px){
    flex-direction: column;
    align-items: center;
  }
}
.footer {
  padding-bottom: 50px;
  @media(max-width: 1200px) {
    flex-direction: column;
    align-items: center;
  }
  .form {
    @include display-flex;
    
  }
  h4 {
    @include content1-desktop;
    color: $txt-primary;
  }
  > div {
    @media(max-width: 1200px) {
    text-align: center;
    }
  }
  input {
    &::placeholder {
      @include content1-desktop;
    }
    background-color: $bg-secondary;
    border-radius: 15px;
    width: 301px;
    height: 50px;
    box-sizing: border-box;
    border: none;
    @include content1-desktop;
    padding: 9px 0 9px 30px;
  }
}

.nav-links {
  @include display-flex;
  a {
    @include header3-desktop;
    margin: 0 15px;
    text-decoration: none;
    transition: 0.1s all ease;
    &:hover {
      opacity: 0.6;
    }
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  @media(max-width: 1200px) {
    flex-direction: column;
    align-items: center;
    a{
      margin: 10px 0;
    }
  }
}

.offer {
  display: flex;
  padding: 70px 50px;
  @media(max-width:1200px){
    flex-direction: column;
    align-items: center;
  }
  justify-content: space-around;
  > div {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    @media(max-width:1200px){
      align-items: center;
      margin-bottom: 50px;
    }
  }
  .question{
    margin-right: 25%;
    @media(max-width: 1200px) {
      text-align: center;
      margin: 0 auto;
    }
  }
  h1 {
    @include header1-desktop;
    margin: 0;
  }
  p {
    @include content1-desktop;
    margin-right: 50px;
    @media(max-width: 1200px) {
      margin-right: 0;
    }
  }
  .button-yellow {
    @include btn-violet;
    background-color: $btn-yellow;
    margin: 0;
    max-width: 175px;
    padding: 9px 37px;
    box-sizing: border-box;
  }
  .main__img {
    background-color: $bg-secondary;
    height: min-content;
    border-radius: 20px;
    padding: 0 10px;
  }
}

.partners {
  display: flex;
  justify-content: center;
  border-top: 3px solid $bg-secondary;
  border-bottom: 3px solid $bg-secondary;
  width: 100%;
  padding: 80px 0;
  img {
    display: block;
    object-fit: contain;
    width: min-content;
  }
}

.work{
  padding: 100px 50px;
  div {
    background-color: rgba(251, 228, 171, 1);
    border-radius: 20px;
    img{
      display: block;
      margin:  0 auto;
      width: 100%;
    }
  }
}

.advantages{
  display: flex;
  justify-content: space-evenly;
  @media(max-width:1000px){
    flex-direction: column;
    align-items: center;
  }

  .card {
    @include display-flex;
  justify-content: flex-start;
  flex: 1;
    margin: 0 50px;
    flex-direction: column;
    @media(max-width:1000px){
      width: 30%;
      margin: 10px 0;
    }
    .card__title{
      @include header3-desktop;
      margin: 40px 0 10px;
      @media(max-width:1000px){
        margin: 10px 0 5px;
      }

    } 
    .card__text{
      @include content1-desktop;
      text-align: center;
      
    }
  }
}

.testimonials {
  display: flex;
  justify-content: center;
  padding: 80px 50px;

  .testimonial{
    display: flex;
    padding: 5% 16%;
    max-width: 1080px;
    width: 100%;
    box-sizing: border-box;
    flex-direction: column;
    justify-content:center;
    align-items: flex-start;
    background-color: rgba(191, 175, 242, 1);
    border-radius: 20px;

    h5 {
      @include content2-desktop;
      color: #414141;
    }
    .person {
      display: flex;
      align-items: center;
     
      > img {
        border: 2px solid rgba(188, 188, 188, 1);
        border-radius: 100%;
        margin-right: 20px;
      }
      .rating{
        display: flex;
        margin-top: 5px;
        .no-fill {
          color: $txt-secondary;
        }
      }
      .name{
        @include content1-desktop;
        color: $txt-secondary;
      }
    }
  }
}